.dialogs {
    display: grid;
    grid-template-columns: 2fr 8fr;
}

.dialogs a {
    color: #13157a;
    text-decoration: none;
}

.dialogsItems {
    padding: 10px;
    width: 180px;
}


.dialogsItems .active {
    color: rgb(255, 255, 0);

}

.dialog {
    padding: 2px;
}

.dialog img {
    width: 40px;
    height: 40px;
    border-radius: 35px;
}

